.home {
  justify-content: center;
  text-align: center;
  flex-direction: column;


}

.title{
  width: 1920px;
  height: 188px;
  margin: auto;
  padding: 62px 633.5px 58px 634.5px;
  text-align: center;
  display : flex;
  justify-content : center;
  align-items : center;
}
.title > h1{
  width: 1920px;
  font-family: Roboto;
  font-weight: bold;
  line-height: 1.31;
  color: #000000;
  font-size: 80px;
  white-space: nowrap;
}
.menu{
  width: 1920px;
  height: 356px;
  margin: auto;
  padding: 0 630px;
  background-color: #fff;
  display : flex;
  justify-content : center;
  align-items : center;
}
.menu > h3{
  width: 1920px;
  height: 356px;
  margin: 120px 3px 0 0;
  font-family: Roboto;
  font-size: 40px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #555555;
}
.menu > h3 > p {
  font-size: 60px;
  color: #000000;
}